import React, { useEffect, useState } from "react";
import { API_GET_DATA } from "../../../global/constants";

const Contact = () => {
  async function fetchData(setFiledata) {
    const res = await fetch(API_GET_DATA);
    const { data } = await res.json();
    console.log(data);

    // res.json().then((data) => {
    //   console.log(data);
    // });
    setFiledata((prevdata) => (prevdata = data));
  }

  const [Filedata, setFiledata] = useState(() => {
    return [];
  });

  useEffect(() => {
    fetchData(setFiledata);
  }, []);

  const goURL = (sURL) => {
    window.open(sURL, "_blank");
  };

  return (
    <div className="divFileMain">
      <div className="divFileType">
        <label className="labelSelect">網站/互動遊戲開發</label>
        <label>LOGO 形象設計</label>
        <label>Banner 平面設計</label>
        <label>動畫/影片剪輯</label>
      </div>
      <div className="divFileLists">
        <div
          onClick={() => {
            goURL("https://annlin718.github.io/Labar/index.html");
          }}
          className="divFileList"
        >
          <img src={process.env.PUBLIC_URL + "/Files/F1.png"}></img>
          <h3>互動式抽獎程式 - 翻牌</h3>
          <div className="divFileSpecial">
            洗牌動畫、互動式翻牌、Canvas 書寫簽名
          </div>
          <div className="divFileSkill">#HTML #CSS #JavaScript #jQuery</div>
        </div>
      </div>
    </div>
  );
};

export default Contact;
